<template>
  <div>
    <vue-loading :active.sync="this.isLoading" :loader="'bars'" :is-full-page="true" :color="'#e3000b'"/>
    <div class="alert alert-danger alert-dismissible d-flex align-items-center" role="alert" v-if="error">
      <fa-icon :icon="['fas', 'exclamation-triangle']"/>
      <div>
        <template v-if="errLogin">&nbsp;Der Login-Name kann nicht leer sein!</template>
        <br v-if="errLogin === true && errPassword === true">
        <template v-if="errPassword">&nbsp;Das Passwort kann nicht leer sein!</template>
        <br v-if="(errLogin === true || errPassword === true) && errLoginCheck === true">
        <template v-if="errLoginCheck">&nbsp;Falsches Passwort oder Benutzername!</template>
        <br v-if="(errLogin === true || errPassword === true || errLoginCheck === true) && errConnection === true">
        <template v-if="errConnection">&nbsp;Keine Verbindung zum Anmeldeserver!</template>
      </div>
      <button type="button" class="btn-close" data-bs-dismiss="alert" aria-label="Close"></button>
    </div>
    <div>
      <h3>Anmelden</h3>
      <div class="row">
        <div class="col-12 mt-1">
          <label>Login-Name</label>
          <input type="text" class="form-control form-control-lg" v-model="login"/>
        </div>
      </div>
      <div class="row">
        <div class="col-12 mt-1">
          <label>Passwort</label>
          <input type="password" class="form-control form-control-lg" v-model="password"/>
        </div>
      </div>
      <div class="row">
        <div class="col-12 mt-3">
          <div class="d-grid gap-2">
            <button type="button" class="btn btn-primary btn-lg" @click="checkLogin">Anmelden</button>
          </div>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
import axios from 'axios';
import VueLoading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';

export default {
  name: "LoginView",
  components: {VueLoading},
  props: {},
  data() {
    return {
      login: '',
      password: '',
      error: false,
      errLogin: false,
      errPassword: false,
      errLoginCheck: false,
      errConnection: false,
      isLoading: false
    }
  },

  methods: {
    checkLogin() {
      // loading
      this.isLoading = true;
      // reset the values
      this.error = false;
      this.errLogin = false;
      this.errPassword = false;
      this.errLoginCheck = false;
      this.errConnection = false

      // check for minor errors
      if (this.login === '') {
        this.error = true;
        this.errLogin = true;
      }

      if (this.password === '') {
        this.error = true;
        this.errPassword = true;
      }

      // when no error until now we will post the data
      if (this.error === false) {
        axios.post(
            'http://localhost/Galle/pwa/checkLogin',
            {'login-name': this.login, password: this.password},
        ).then((response) => {
          console.log(response.data);
          if (response.data.userid > 0) {
            this.isLoading = false;
            this.$offlineStorage.set('uid', response.data.uid);
            this.$offlineStorage.set('userid', response.data.userid);
            this.$offlineStorage.set('date', response.data.date);

            this.$router.push({name: 'TimeTracking', params: {userId: parseInt(response.data.userId)}})
          } else {
            this.error = true;
            this.errLoginCheck = true;
            this.isLoading = false;
          }
        }).catch((error) => {
          console.log(error);
          this.error = true;
          this.errConnection = true;
          this.isLoading = false;
        });
      } else {
        this.isLoading = false;
      }
    }
  }
}
</script>

