import Vue from 'vue';
import App from './App.vue';
import router from './router';
import wb from "./registerServiceWorker";
import 'bootstrap';
import './icons.config';
import VueOffline from 'vue-offline';

Vue.use(VueOffline);
Vue.prototype.$workbox = wb;
Vue.config.productionTip = false;

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
