<template>
  <div class="container">
  <div id="app">
    <div id="nav">
      <template > <!--v-if="this.$route.name !== 'Login'"-->
        <router-link to="/">Login</router-link> |
        <router-link to="/time-tracking">Zeiterfassung</router-link>
      </template>
    </div>
    <router-view /> <!-- v-bind="myProps" -->
  </div>
  </div>
</template>

<script>
import dayjs from "dayjs";
import "bootstrap/dist/css/bootstrap.min.css";

export default {
  name: "App",
  props: {

  },
  computed: {
    /*
    myProps() {
      if (this.$route.name === 'Login') {
        return {
          checkLoginUrl: localStorage.checkLoginUrl,
          checkLoginToken: localStorage.checkLoginToken
        }
      }
      if (this.$route.name === 'TimeTracking') {
        return {
          timeTrackingUrl: localStorage.timeTrackingUrl,
          timeTrackingToken: localStorage.timeTrackingToken
        }
      }
      return 0;
    }
     */
  },
  mounted() {

  },
  created() {
    if (this.$workbox) {
      this.$workbox.addEventListener("waiting", () => {
        this.showUpdateUI = true;
      });
    }

    // TODO: evaluate if we should check with axios here too
    // when an userid, date and uid is present in storage from this day automatically login
    if (this.$offlineStorage.get('userid') > 0 && this.$offlineStorage.get('uid') !== '' ) {

      if (dayjs(this.$offlineStorage.get('date')).format('YYYY-MM-DD') === dayjs().format('YYYY-MM-DD')) {
        console.log('has date');
        this.$router.push({name: 'TimeTracking', params: {userId: this.$offlineStorage.get('userid')}})
      }
    }
  },
  async accept() {
    this.showUpdateUI = false;
    await this.$workbox.messageSW({ type: "SKIP_WAITING" });
  }
}
</script>

